<template>
  <div class="login">
    <van-nav-bar title="会员登录" left-arrow @click-left="$router.go(-1)" />
    <div class="container">
      <div class="title">
        <h3>手机号登录</h3>
        <p>未注册的手机号登录后将自动注册</p>
      </div>
      <div class="form">
        <!-- 输入手机号码 -->
        <div class="form-item">
          <input v-model="mobile" class="inp" maxlength="11" placeholder="请输入手机号码" type="text">
        </div>
        <!-- 输入图形验证码 -->
        <div class="form-item">
          <input v-model="picCode" class="inp" maxlength="5" placeholder="请输入图形验证码" type="text">
          <img v-if="picUrl" :src="picUrl" @click="getPicCode" alt="">
        </div>
        <!-- 输入短信验证码 -->
        <div class="form-item">
          <input v-model="msgCode" class="inp" placeholder="请输入短信验证码" type="text">
          <button @click="getCode">
            {{ second === totalSecond ? '获取验证码' : second + '秒后重新发送'}}
          </button>
        </div>
      </div>

      <div @click="login" class="login-btn">登录</div>
    </div>
  </div>
</template>

<script>
import { Toast } from 'vant'
import { codeLogin, getMsgCode } from '@/api/login'
import request from '@/utils/request'

export default {
  name: 'LoginPage',
  data () {
    return {
      // 图形验证码
      picUrl: '', // 存储渲染图片的地址
      picKey: '', // 请求传递过来的唯一标识

      // 倒计时数据
      totalSecond: 60, // 总秒数
      second: 60, // 开定时器
      timer: null, // 定时器id

      mobile: '',
      picCode: '', //  图形验证码
      msgCode: '' //   短信验证码
    }
  },
  async created () {
    this.getPicCode()
  },
  methods: {
    // 后台验证 图形验证码是否传入正确 要验证 码正确 和唯一标识key
    async getPicCode () {
      const { data: { base64, key } } = await request.get('/captcha/image')
      // const { data: { base64, key } } = await this.getPicCode('/captcha/image')
      this.picUrl = base64 // 存储地址
      this.picKey = key // 存储标识

      Toast('获取图形验证码成功')
      // 组件内不用导入import { Toast } from 'vant'
      // this.$toast('直接就可以使用')
      // this.$toast.success('成功文案')
    },

    // 校验 手机号和图形验证码是否合法
    validFn () {
      const regex = /^1[3-9]\d{9}$/
      const dcgex = /^\w{4}$/
      if (!regex.test(this.mobile)) {
        this.$toast('请输入正确手机号')
        return false
      }
      if (!dcgex.test(this.picCode)) {
        this.$toast('请输入正确验证码')
        return false
      }
      return true
    },

    // 获取短信验证码倒计时
    async getCode () {
      if (!this.validFn()) {
        // 如果没通过校验
        this.$toast('验证不通过')
        return
      }
      // 当目前没有定时器开着 且totalscond和second 才可以开启倒计时
      if (!this.timer && this.totalSecond === this.second) {
        // 发送请求
        // 如果 响应的status 非200 最好抛出一个promise错误 await只会等待正确的promise
        const res = await getMsgCode(this.picCode, this.picKey, this.mobile)
        console.log(res)
        this.$toast('发送成功，请注意查收')

        // 倒计时
        this.timer = setInterval(() => {
          this.second--
          if (this.second < 1) {
            clearInterval(this.timer)
            this.timer = null // 重置定时器
            this.second = this.totalSecond // 事件归位
          }
        }, 1000)
      }
    },

    // 登录
    async login () {
      if (!this.validFn()) {
        return
      }
      const dxgex = /^\d{6}$/
      if (!dxgex.test(this.msgCode)) {
        this.$toast('请输入正确验证码')
        return
      }
      const res = await codeLogin(this.mobile, this.msgCode)
      // 将请求成功的res的数据存入vuex
      this.$store.commit('user/setUserInfo', res.data)
      // console.log(res)
      this.$toast('登陆成功')

      // 点击确认登录按钮判断 有无会跳地址
      // 如果有 说明是从其它页面回调过来 拦截登录的 需要回跳
      // 如果没有 登陆后 正常去首页 即可
      const url = this.$route.query.backUrl || '/'

      // 用replace替代 push 可以将当前的历史纪录替换为跳转后的页面
      // 减少路由历史路线
      this.$router.replace(url)
    }
  },
  // 在销毁页面组件时清除定时器
  destroyed () {
    clearInterval(this.timer)
  }
}
</script>

<style lang="less" scoped>
.container {
  padding: 49px 29px;

  .title {
    margin-bottom: 20px;
    h3 {
      font-size: 26px;
      font-weight: normal;
    }
    p {
      line-height: 40px;
      font-size: 14px;
      color: #b8b8b8;
    }
  }

  .form-item {
    border-bottom: 1px solid #f3f1f2;
    padding: 8px;
    margin-bottom: 14px;
    display: flex;
    align-items: center;
    .inp {
      display: block;
      border: none;
      outline: none;
      height: 32px;
      font-size: 14px;
      flex: 1;
    }
    img {
      width: 94px;
      height: 31px;
    }
    button {
      height: 31px;
      border: none;
      font-size: 13px;
      color: #cea26a;
      background-color: transparent;
      padding-right: 9px;
    }
  }

  .login-btn {
    width: 100%;
    height: 42px;
    margin-top: 39px;
    background: linear-gradient(90deg,#ecb53c,#ff9211);
    color: #fff;
    border-radius: 39px;
    box-shadow: 0 10px 20px 0 rgba(0,0,0,.1);
    letter-spacing: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
</style>
